<template>
  <div class="w-100 bg-white custom-chart-div t-min-height Linechrt text-center">
    <div :id="'lineDiv_' + moduleCode + '_' + action + index">
      <loader :isVisible="isLoading"></loader>
      <div
        :class="{
          'line d-inline-block mt-2 reportBlock t-min-height align-center c3 w-100':
            LineWidgetData.length > 0,
          'line d-inline-block mt-2 align-center c3 w-100':
            LineWidgetData.length == 0,
        }"
        :controller="moduleCode"
        :id="'div_LineChart_' + action + index"
        :style="{
          'width:60%; float:left; text-align:center;':
            LineWidgetData.length > 0,
          'width:100%;text-align:center;': LineWidgetData.length == 0,
        }"
      ></div>
    </div>
    <div v-if="LineWidgetData.length === 0 && isLoading === false" class="text-danger no-record">
      {{ $t("NoRecordfound") }}
    </div>
  </div>
</template>

<script>
import "d3";
import "c3";
import "c3/c3.min.css";
import DataService from "../../../services/DataService";

export default {
  props: {
    reportName: null,
    chartType: String, // Set the type
    // selectedChartType: String,
    moduleCode: null,
    fromDate: null,
    toDate: null,
    index: null,
    width: null,
    height: null,
    reportId: null,
  },
  data() {
    return {
      isLoading: true,
      action: this.reportName,
      charT_TYPE: "line",
      LineWidgetData: [],
      HeaderData: null,
      showAddDashboardReport: false,
      ChartName: null,
    };
  },
  watch: {
    width: function () {
      this.bindWidget(this);
    },
    height: function () {
      this.bindWidget(this);
    },
    toDate: function () {
      this.FetchData();
    },
    fromDate: function () {
      this.FetchData();
    },
    // selectedChartType(newChartType) {
    //   // Update the chart type
    //   this.charT_TYPE = newChartType.toLowerCase();
    //   // Re-fetch data or re-render the chart
    //   this.FetchData();
    //   this.bindWidget();
    // },
  },
  created() {
    this.FetchData();
    this.action = this.action.replace(/\s/g, "");
  },
  methods: {
    async FetchData() {
      var vueObj = this;
      vueObj.isLoading = true;
      var reportidcheck = vueObj.reportId == undefined ? 0 : vueObj.reportId;
      var params = `moduleName=${vueObj.moduleCode}&reportName=${vueObj.reportName}&&fromDate=${vueObj.fromDate}&toDate=${vueObj.toDate}&ReportId=${reportidcheck}&usersDash=`;
      await DataService.GetDashboardData(params).then(function (response) {
        if (response.data != null && response.data != "") {
          if (response.data.status == "Success") {
            if (response.data.result != null) {
              vueObj.LineWidgetData = response.data.result.data;
              vueObj.bindWidget(vueObj);
            }
          }
        }
        vueObj.isLoading = false;
      });
    },
    bindWidget: function (vueObj) {
      if (vueObj.charT_TYPE && vueObj.LineWidgetData.length > 0) {
        var chartType = this.charT_TYPE;
        var uniqueWidgetTypes = [
          ...new Set(vueObj.LineWidgetData.map((item) => item.WIDGET_TYPE)),
        ];
        // var xValues = uniqueWidgetTypes;
        var xValues = uniqueWidgetTypes;
        var yValues = [vueObj.reportName];
        uniqueWidgetTypes.forEach((widgetType) => {
          var valuesForWidgetType = vueObj.LineWidgetData.filter(
            (item) => item.WIDGET_TYPE === widgetType
          ).map((item) => item.VALUE);
          // yValues.push(widgetType);
          yValues = yValues.concat(valuesForWidgetType);
        });
        var columns = [xValues, yValues];
        var selector = "#div_LineChart_" + vueObj.action + vueObj.index;
        // eslint-disable-next-line no-undef
        $(selector).html("");
        // eslint-disable-next-line no-undef
        let LineChart = c3.generate({
          bindto: selector,
          data: {
            columns: columns,
            type: chartType,
            axes: {
              data2: "y2", // ADD
            },
          },
          axis: {
            y2: {
              show: true, // ADD
            },
          },
        });
        // eslint-disable-next-line no-undef
        $(selector).data("c3-chart", LineChart);
      }
    },
  },
};
</script>

<style src="c3/c3.css"></style>
<style>
.custom-chart-div.Linechrt .reportBlock {
  transform: initial;
}
</style>
