<template>
  <div class="w-100 bg-white custom-chart-div t-min-height Barchrt text-center">
    <div
      v-if="BarWidgetData"
      :id="'BarChart_' + moduleCode + '_' + action + index"
    >
      <loader :isVisible="isLoading"></loader>
      <div
        v-bind:class="{
          'bar d-inline-block mt-2 reportBlock t-min-height align-center c3 w-100':
            BarWidgetData.length > 0,
          'bar d-inline-block mt-2 align-center c3 w-100':
            BarWidgetData.length == 0,
        }"
        :controller="moduleCode"
        :id="'div_BarChart_' + action + index"
        v-bind:style="{
          'width:60%; float:left; text-align:center;': BarWidgetData.length > 0,
          'width:100%;text-align:center;': BarWidgetData.length == 0,
        }"
      ></div>
    </div>
    <div
      v-if="BarWidgetData.length === 0 && isLoading === false"
      class="text-danger no-record"
    >
      {{ $t("NoRecordfound") }}
    </div>
  </div>
</template>

<script>
import "d3";
import "c3";
import "c3/c3.min.css";
import DataService from "../../../services/DataService";
export default {
    props: {
        reportName: null,
        chartType: String, // Set the type
        // selectedChartType: String,
        moduleCode: null,
        fromDate: null,
        toDate: null,
        index: null,
        reportId: null,
        width: null,
        height: null,
    },
    data() {
        return {
        isLoading: true,
        action: "Lead_By_Status",
        charT_TYPE: "bar",
        BarWidgetData: [],
        HeaderData: null,
        showAddDashboardReport: false,
        chart: null, // Add a chart property to store the C3.js chart instance
        };
    },
    created() {
        this.FetchData();
    },
    watch: {
        width: 'handleResize',
        height: 'handleResize',
        toDate: 'handleFetchData',
        fromDate: 'handleFetchData',
        chartType: 'handleFetchData'
    },
    methods: {
        async FetchData() {
            var vueObj = this;
            vueObj.isLoading = true;
            var reportidcheck = vueObj.reportId == undefined ? 0 : vueObj.reportId;
            var params = `moduleName=${vueObj.moduleCode}&reportName=${vueObj.reportName}&fromDate=${vueObj.fromDate}&toDate=${vueObj.toDate}&ReportId=${reportidcheck}&usersDash=`;
            await DataService.GetDashboardData(params).then(function (response) {
                if (response.data != null && response.data != "") {
                    if (response.data.status == "Success") {
                        if (response.data.result != null) {
                            vueObj.BarWidgetData = response.data.result.data;
                            vueObj.bindWidget(vueObj);
                        }
                    }
                }
                vueObj.isLoading = false;
            });
        },
        bindWidget(vueObj) {
            if (vueObj.charT_TYPE && vueObj.BarWidgetData.length > 0) {
                var arrAU = [];
                // eslint-disable-next-line no-undef
                $.each(vueObj.BarWidgetData, function (index, element) {
                    arrAU.push([element.NAME, element.VALUE]);
                });
                var xCategory = "";
                var yCategory = "";
                if (vueObj.action == "ProjectsStatusWise") {
                    xCategory = vueObj.$t("Status");
                    yCategory = vueObj.$t("Projects");
                } else if (vueObj.action == "Lead_By_Status") {
                    xCategory = vueObj.$t("Status");
                    yCategory = vueObj.$t("Leads");
                } else {
                    xCategory = vueObj.$t("User");
                    yCategory = vueObj.$t("Tickets");
                }
                var selector = "#div_BarChart_" + vueObj.action + vueObj.index;
                // eslint-disable-next-line no-undef
                $(selector).html("");
                let c3Obj = {
                    bindto: selector,
                    data: {
                        columns: arrAU,
                        type: "bar",
                        empty: {
                            label: {
                                position: "right",
                                text: vueObj.$t("NoRecordfound"),
                            },
                        },
                        onclick: function() {
                            if (this.moduleName == "CRM") {
                                window.open("javascript:;");
                            } else {
                                window.open(
                                "/Project/Index?searchCondition=PR.STATUS_ID in(1001)",
                                "_blank"
                                );
                            }
                        },
                    },
                    bar: {
                        label: {
                            format: function (value) {
                                // eslint-disable-next-line no-undef
                                return d3.format("")(value);
                            },
                        },
                        space: 0.15,
                        width: {
                            ratio: arrAU.length > 4 ? 0.5 : 0.3,
                        },
                    },
                    legend: {
                        show: true,
                        position: "bottom",
                    },
                    color: {
                        pattern: [
                        "#23A4FF",
                        "#FF6B6B",
                        "#5FB49C",
                        "#776274",
                        "#916953",
                        "#28587B",
                        "#F4B701",
                        "#272635",
                        "#AEE5D8",
                        "#FFC482",
                        "#3C6E71",
                        "#9FA0FF",
                        "#D1B1CB",
                        "#95B2B8",
                        "#EF8354",
                        "#72D9C8",
                        "#C6EBBE",
                        "#56203D",
                        "#A09CB0",
                        "#5C573E",
                        ],
                    },
                    axis: {
                        rotated: vueObj.chartType == "Bar" ? false : true,
                        x: {
                            label: {
                                text: "",
                                position: "outer-center",
                            },
                            categories: [xCategory],
                            type: "category",
                        },
                        y: {
                            label: {
                                text: yCategory,
                                position: "outer-center",
                            },
                            padding: {
                                top: 20,
                            },
                        },
                    },
                };
                // eslint-disable-next-line no-undef
                var Barchrt = c3.generate(c3Obj);
                // eslint-disable-next-line no-undef
                $(selector).data("c3-chart", Barchrt);
            }
        },
        handleResize() {
            this.bindWidget(this);
        },
        handleFetchData() {
            this.FetchData();
        }
    },
};
</script>
<style src="c3/c3.css"></style>
